/* ==================== common styles ======================= */
html {
  font-size: 10px;
  overflow-x: hidden;
  /*used for rem-units*/
}
body {
  min-width: 320px;
  color: $creg;
  font-family: $fmain;
  background: #050505;
  font-size: 16px;
  overflow-x: hidden;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.hidden {
  display: none;
}
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0, .8);
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
a {
  text-decoration: none;
  color: inherit;
}
nav a.active {
  background-color: rgba(0,0,0,.7);
}
img
input,
textarea {
  outline: none;
  border: none;
  &:focus {
    outline: none;
    border: none;
  }
}
textarea {
  resize: none;
  height: 60px;
}
::-webkit-input-placeholder {
  color: #ccc;
  font-size: 12px;
  font-family: $fmain;
}
:-ms-input-placeholder {
  color: #ccc;
  font-size: 12px;
  font-family: $fmain;
}
::-ms-input-placeholder {
  color: #ccc;
  font-size: 12px;
  font-family: $fmain;
}
::placeholder {
  color: #ccc;
  font-size: 12px;
  font-family: $fmain;
}
section {
  position: relative;
  padding: 5rem 0 3rem;
}
.wrapper {
  position: relative;
  width: 97%;
  max-width: 1140px;
  margin: 0 auto;
}
h1,
h2,
h3 {
  font-weight: 700;
}
h2 {
  font-size: 30px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 3px;
}
h3 {
  font-size: 16px;
}
h4 {
  font-size: 14px;
}
p {
  font-size: 16px;
  line-height: 1.875;
  letter-spacing: .8px;
  max-width: 100%;
}
.glare {
  position: absolute;
}
/*------------------------------ header ------------------------------*/
.logo {
  position: relative;
}
.headerTop {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  padding: 2.9rem 0 1.8rem;
}
#animation_container {
  width: 210px;
  height: 45px;
}
#canvas {
  width: 210px;
  height: 45px;
  position: absolute;
  display: block;
}
#dom_overlay_container {
  display: block;
  pointer-events: none;
  overflow: hidden;
  width: 210px;
  height: 45px;
  left: 0;
  top: 0;
}
.headerTop_contacts {
  font-weight: 300;
  letter-spacing: .3px;
  span {
    color: $cbr;
  }
}
.contacts_links,
.headerTop_lang-list {
  display: inline-block;
  li {
    display: inline-block;
    margin-bottom: 10px;
  }
}
.contacts_links {
  li {
    padding: 0 14px;
    border-right: 1px solid $cbr;
    &:last-child {
      border-right: 0;
    }
  }
}
.phoneLink,
.mailLink {
  display: inline-block;
}
.phoneLink:first-of-type {
  &:before {
    content: "";
    display: inline-block;
    margin-right: 7px;
  @include sprite($s-phone);
  }
}
.mailLink {
  font-size: 13px;
  font-weight: 400;
  &:before {
    content: "";
    display: inline-block;
    margin-right: 7px;
  @include sprite($s-mail);
  }
}
.lang_ru {
  display: inline-block;
  @include sprite($s-ru);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  opacity: .7;
  &:hover {
    opacity: 1;
  }
}
.lang_en {
  display: inline-block;
  @include sprite($s-en);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  margin-left: 23px;
  opacity: .7;
  &:hover {
    opacity: 1;
  }
}
.headerNav {
  background: $cbr;
  font-size: 14px;
