  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  letter-spacing: 1px;
  li {
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  a {
    display: block;
    padding: 18px 1vw;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    text-transform: uppercase;
    white-space: nowrap;
    background-image: none;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    &:hover {
      background-color: rgba(0,0,0,.7);
      background-image: url(../img/geometryForms/corners-w.png);
      background-size: 90% 80%;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      color: #0f0;
    }
  }
}
.searchBtn {
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 50px;
  right: 0;
  .searchBtn-trigger {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../img/commonImgs/search.png);
    background-repeat: no-repeat;
    background-position: 50% 50%;padding: 0;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    cursor: pointer;
    &:hover {
      background-color: rgba(0, 0, 0, 0.7);
    }
  }
}
.form-wrap {
  width: 500px;
  height: 50px;
}
.search-form {
  display: block;
  width: 0px;
  overflow: hidden;
  position: absolute;
  right: 0;
  bottom: -36px;
  padding-right: 0px !important;
  z-index: 10;
  opacity: 1;
  border: 1px solid transparent;
  -webkit-transition: width .5s, border 1s;
  -o-transition: width .5s, border 1s;
  transition: width .5s, border 1s;
  &.active {
    width: 240px;
    padding-right: 16px;
    border: 1px solid $cbr;
    .search-close {
      opacity: 1;
    }
  }
  .search-close {
    opacity: 0;
    position: absolute;
    top: 9px;
    right: 2px;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    cursor: pointer;
  }
  input {
    height: 28px;
    line-height: 28px;
    width: 300px;
    padding: 3px 5px;
    background: transparent;
    color: #fff;
    opacity: 1;
    border: none !important;
    outline: none !important;
  }
}
.mob-nav-btn {
  position: fixed;
  top: 75px;
  right: 30px;
  width: 35px;
  height: 35px;
  background: #050505;
  border: 1px solid $cbr;
  cursor: pointer;
  z-index: 10;
  display: none;
  span {
    display: block;
    background: $cbr;
    position: absolute;
    top: 16px;
    left: 5px;
    right: 5px;
    height: 2px;
    border: none;
    &:before,
    &:after {
      content: "";
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: 2px;
      background: $cbr;
    }
    &:before {
      top: -8px;
    }
    &:after {
      top: 8px;
    }
  }
}
.mob-nav-cont {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -320px;
  width: 320px;
  background: rgba(0,0,0,.9);
  overflow-y: auto;
  padding: 40px 15px;
  z-index: 11;
  -webkit-transition: all .7s;
  -o-transition: all .7s;
  transition: all .7s;
  &.active {
    left: 0;
  }
}
.mob-logo {
  width: 150px;
  display: block;
  margin: 0 auto 25px;
}
.mobNav_list {
  li {
    margin: 20px 0;
    a {
      text-transform: uppercase;
      font-size: 15px;
    }
  }
}
.mob-nav-close,
.modal-close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 20px;
  cursor: pointer;
}
/*------------------------------ banner section ------------------------------*/
.banner-section {
  padding: 0;
  img {
    width: 100%;
  }
}
/*------------------------------ works slider section ------------------------------*/
.worksSlider-section {
  padding: 6rem 0 6rem;
}
.heading-container {
