.hex-container {
  display: block;
  width: 57px;
  height: 59px;
  position: absolute;
  left: -11px;
  top: 9%;
}
.hex {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}
.sliderContainer {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  overflow: hidden;
}
.sliderArrowsContainer {
  background: $cd;
  position: absolute;
  right: -5%;
  bottom: -4px;
  height: 51px;
  width: 35%;
  -webkit-transform: skew(-30deg);
      -ms-transform: skew(-30deg);
          transform: skew(-30deg);
  padding: 10px 10px 0px 34px;
  overflow-x: hidden;
  white-space: nowrap;
}
.sliderArrow {
  display: inline-block;
  width: 66px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  border: 1px solid $cbr;
  cursor: pointer;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  -webkit-box-shadow: 0px 0px 0px $cbr, inset 0px 0px 0px $cbr;
          box-shadow: 0px 0px 0px $cbr, inset 0px 0px 0px $cbr;
  &:hover {
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    -webkit-box-shadow: 0px 0px 20px $cbr, inset 0px 0px 20px $cbr;
            box-shadow: 0px 0px 20px $cbr, inset 0px 0px 20px $cbr;
  }
  span {
    display: inline-block;
    -webkit-transform: skew(30deg);
        -ms-transform: skew(30deg);
            transform: skew(30deg);
   }
}
.sliderArrow_prev {
  span {
    @include sprite($s-arrPrev);
  }
}
.sliderArrow_next {
  span {
    @include sprite($s-arrNext);
  }
}
/*------------------------------ geography section ------------------------------*/
.geography-section {
  padding: 6px 0 5px
}
.mapContainer {
  max-width: 1920px;
  margin: 0 auto;
  background-image: url(../img/earth.jpg);
  background-repeat: no-repeat;
  background-position: 50% 0%;
  background-size: auto 100%;
}
.pinsContainer {
  position: relative;
  width: 550px;
  height: 100vh;
  min-height: 380px;
  max-height: 380px;
  margin: 0 auto;
}
.mapPin {
  display: block;
  position: absolute;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  cursor: pointer;
  &:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
    color: rgba(255,255,255, 1);
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    -webkit-animation: none;
            animation: none;
    .pin-img-container:before {
      -webkit-box-shadow: 0px 0px 10px $cbr;
              box-shadow: 0px 0px 10px $cbr;
      -webkit-animation: pinrotate 1.5s linear infinite;
              animation: pinrotate 1.5s linear infinite;
    }
  }
}
.map-link {
  display: block;
  text-align: center;
  color: rgba(255,255,255, .7);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  cursor: pointer;
  &:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
    color: rgba(255,255,255, 1);
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    -webkit-animation: none;
            animation: none;
  }
}
.pin-img-container {
  display: inline-block;
  position: relative;
  padding: 5px;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  border-radius: 50%;
  &:hover {
    -webkit-box-shadow: 0 0 5px $cbr;
            box-shadow: 0 0 5px $cbr;
  }
  img {
    position: relative;
    display: block;
    margin: 0 auto;
  }
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    border-radius: 50%;
    -webkit-box-shadow: 0px 0px 10px transparent;
            box-shadow: 0px 0px 10px transparent;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    bottom: -5px;
    left: 18%;
    width: 26px;
    height: 22px;
    background-image: url(../img/commonImgs/pinlight.png);
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}
.map-title {
