
/* ==================== css-animations ======================= */

@keyframes blink1 {
    0% {
        filter: brightness(75%);
    }
    30% {
        filter: brightness(500%) ;
    }
    70% {
        filter: brightness(75%);
    }
    100% {
        filter: brightness(75%);
    }
}

@keyframes horizontalSlide {
    0% {
        left: -100%;
        right: 0%;
    }
    100% {
        left: 0%;
        right: -100%;
    }
}


@keyframes lines2 {
    0% {
        bottom: 0%;
    }
    5% {
        bottom: 30%;
    }
    10% {
        bottom: 38%;
    }
    15% {
        bottom: 28%;
    }
    20% {
        bottom: 48%;
    }
    25% {
        bottom: 35%;
    }
    30% {
        bottom: 45%;
    }
    35% {
        bottom: 30%;
    }
    40% {
        bottom: 50%;
    }
    45% {
        bottom: 55%;
    }
    50% {
        bottom: 50%;
    }
    55% {
        bottom: 57%;
    }
    60% {
        bottom: 50%;
    }
    65% {
        bottom: 62%;
    }
    70% {
        bottom: 60%;
    }
    75% {
        bottom: 68%;
    }
    80% {
        bottom: 88%;
    }
    85% {
        bottom: 100%;
    }
    90% {
        bottom: 42%;
    }
    95% {
        bottom: 50%;
    }
    100% {
        bottom: 0%;
    }
}

@keyframes brightness {
    0% {
        filter: brightness(100%);
    }
    80% {
        filter: brightness(100%);
    }
    50% {
        filter: brightness(300%);
    }
    80% {
        filter: brightness(100%);
    }
    100% {
        filter: brightness(100%);
    }
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
        opacity: 1;
    }
    50% {
        transform: rotate(180deg);
        opacity: 0;
    }
    100% {
        transform: rotate(360deg);
        opacity: 1;
    }
}

@keyframes pinrotate {
    0% {
        transform: rotateY(0deg);
    }
    100% {
        transform: rotateY(360deg);
    }
}

@keyframes huerotate {
    0% {
        filter: grayscale(100%);
    }
    100% {
        filter: grayscale(0%);
    }
}
