/* ==================== Media queries ======================= */
/*==========  Desktop First Method  ==========*/
/* Large Devices, Wide Screens */
@media only screen and (max-width : 1600px) {
    .corner-hex-container {
      bottom: -30px;
      right: -103px;
      width: 165px;
      height: 185px;
    }
    .installation-img-container .corner-hex-container {
      width: 165px;
      height: 195px;
      bottom: -57px;
      right: -93px;
    }
    .production-block .corner-hex-container {
      bottom: -60px;
      right: -100px;
      height: 190px;
    }
}
@media only screen and (max-width : 1300px) {
    .corner-hex-container {
        display: none;
    }
}
@media only screen and (max-width : 1200px) {
    html {
        font-size: 8px;
    }
    .decor-horizontal:after {
        display: none;
    }
    .headerNav_list a {
        padding: 10px 5px;
    }
    .advantages-list,
    .about-text {
        flex-basis: 100%;
    }
    .about-text {
        max-width: 100%;
    }
    .lines-decor-cont {
        height: 100%;
    }
    .sliderArrowsContainer {
        padding: 10px 10px 0px 10px;
    }
    .sliderArrowsContainer {
        height: 42px;
        width: 20%;
    }
    .sliderArrow {
        height: 26px;
        line-height: 26px;
    }
    .contacts-links-part {
        flex-basis: 100%;
    }
    .contacts-container {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .contacts-links-part {
        max-width: 100%;
    }
    .form-decor {
        display: none;
    }
    .contacts-form-part {
        padding: 3%;
    }
    .footer-top {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .footer-links-cont {
        width: 100%;
    }
    .team-item-container {
        flex-basis: 50%;
        max-width: 100%;
        text-align: center;
    }
    .team-item-img {
        margin: 0 auto 7rem;
    }
    .team-photo-hex {
        left: 30.6%;
    }
    .team-item-name {
        min-height: 28px;
    }
    .team-item-text {
        padding-left: 0;
    }
    .slide-text-container,
    .installation-img-text,
    .production-img-text {
        width: 100%;
        max-width: 100%;
        top: auto;
        bottom: 0;
        height: auto;
        padding: 1% 1% 1% 20px;
        p {
            line-height: 1.2;
        }
    }
    .slide-text-container .sliderArrowsContainer {
        height: auto;
        bottom: auto;
        right: 5%;
        top: 101%;
    }
}
@media only screen and (max-width : 1024px) {
    p {
        font-size: 14px;
    }
    .headerTop {
        flex-wrap: wrap;
    }
    .headerTop_contacts {
        order: 2;
        flex-basis: 100%;
        text-align: center;
        margin-top: 15px;
    }
    .heading {
        height: 70px;
        line-height: 62px;
    }
    h2 {
        font-size: 23px;
    }
    .hex-container {
        width: 42px;
        height: 44px;
        left: 0;
    }
    .sliderArrowsContainer {
        width: 175px;
        right: -40px;
    }
    .sliderArrow {
        width: 45px;
    }
    .about-content {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .footer-pages-links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        li {
            flex-basis: 50%;
            margin-bottom: 20px;
            padding: 0 10px !important;
        }
    }
    .footer-contacts_links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
        li {
            flex-basis: 50%;
            width: 49%;
            text-align: left !important;
            padding: 0 10px !important;
            margin-bottom: 20px;
            &:before {
                display: none !important;
            }
        }
    }
    .copy,
    .creator {
        flex-basis: 50%;
    }
    .team-photo-hex {
        display: none;
    }
    .gallery-slider-container .sliderArrowsContainer {
      width: 15.3%;
      height: 45px;
    }
}
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
    html {
        font-size: 7px;
    }
    .headerNav_list a {
        font-size: 11px;
    }
    .sliderArrowsContainer,
    .aboutSlider-section .sliderArrowsContainer {
        height: auto;
        padding-top: 6px;
        padding-left: 6px;
        bottom: auto;
        top: 105%;
        right: 0;
        background: transparent;
    }
    .sliderArrow {
        height: 20px;
        line-height: 20px;
    }
    .arrows-bg {
        background-image: none;
    }
    .about-text-section {
        padding-top: 90px;
    }
    .slide-text-container p,
    .installation-img-text p,
    .production-img-text p {
        font-size: 12px;
    }
    .gallery-slider-container {
        overflow: visible;
    }
    .gallery-slider-text {
        padding: 8px 10px;
        top: auto;
        left: 20px;
        right: 0;
        bottom: 0;
        width: 100%;
        height: auto;
        transform: none;
        p {
            position: static;
            max-width: 100%;
            font-size: 12px;
            line-height: 1.2;
            transform: none;
        }
    }
    .gallery-slider-container .sliderArrowsContainer {
        width: auto;
    }
    .main-contacts-blocks .contacts-links-part > div {
        padding-right: 10px;
    }
    .main-contacts-links {
      .contact-team,
      .contact-shceme,
      .contact-message {
          padding: 0 22px;
      }
    }
    .sliderContainer,
    .sliderArrowsContainer {
        overflow: visible !important;
    }
}
@media only screen and (max-width : 890px) {
    .headerNav_list {
        display: none;
    }
    .mob-nav-btn {
        display: block;
    }
    .headerNav{
        background: transparent;
    }
    .contacts_links li {
        border-right: 0;
        padding: 0 5px;
        a,
        .phone-link {
            font-size: 14px;
        }
    }
    .search-form {
        top: 0px;
        bottom: auto;
        right: 100%;
        margin-right: 5px;
        padding-right: 25px;
        input {
            background: transparent;
            border: 1px solid #fff;
            color: #fff;
            width: 200px;
        }
        ::-webkit-input-placeholder {
          color: #ccc;
        }
        ::-moz-placeholder {
          color: #ccc;
        }
        :-ms-input-placeholder {
          color: #ccc;
        }
        ::placeholder {
          color: #ccc;
        }
    }
}
/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
    html {
        font-size: 4px;
    }
    .subheading {
      font-size: 15px !important;
      height: 30px;
      line-height: 30px;
    }
    .contacts_links {
        display: block;
        flex-basis: 100%;
        margin-top: 25px;
    }
    .advantages-text {
        letter-spacing: 0;
    }
    .contacts-form-part {
        padding: 3%;
    }
    .team-item-container {
        padding: 20px 10px;
    }
    .team-item-container {
        flex-basis: 100%;
    }
    .team-item-name {
        min-height: 1px;
        margin-bottom: 5px;
    }
    .team-item-img {
        margin-bottom: 10px;
    }
    .team-photo-shevron {
        display: none;
    }
    .about-text-section p {
        font-size: 14px;
    }
    .clients-section {
        padding: 3rem 0;
    }
    .slide-text-container,
    .installation-img-text,
    .production-img-text  {
       position: relative;
    }
    .slide-text-container .sliderArrowsContainer {
        bottom: auto;
        top: -34px;
    }
    .gallery-slider-text {
        position: static;
    }
    .sliderArrowsContainer {
        width: auto;
    }
    .gallery-slider-container {
        padding-left: 0;
    }
    .gallery-slider-container .lines-decor-cont {
        display: none;
    }
    .main-contacts-blocks .contacts-links-part .lines-decor-cont {
        display: none;
    }
    .main-contacts-blocks .contacts-links-part {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .main-contacts-blocks .contacts-links-part > div {
        flex-basis: 48%;
    }
    .main-contacts-links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        li:first-child {
            flex-basis: 100%;
            margin-bottom: 25px;
        }
        li:last-child {
            a:after {
                display: none;
            }
        }
        .contact-team {
            padding-left: 0;
        }
    }
}
@media only screen and (max-width : 650px) {
    .main-form {
        display: block;
        text-align: center;
        input {
            min-width: 60%;
        }
    }
}
@media only screen and (max-width : 600px) {
    .mapContainer {
        display: none;
    }
    .mob-locations-cont {
        display: block;
    }
    .contacts-form-part .main-form > div {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 0;
        .captcha,
        input[type="submit"] {
            flex-basis: 100%;
            display: block;
        }
        input[type="submit"] {
            margin: 20px 50px;
        }
    }
    .gallery-links li:last-child a:after {
        display: none;
    }
    .main-contacts-blocks .contacts-links-part > div {
        flex-basis: 100%;
        padding-left: 0;
    }
    .main-contacts-links li {
        flex-basis: 100%;
        margin-bottom: 25px;
    }
    .main-contacts-links .contact-team, .main-contacts-links .contact-shceme, .main-contacts-links .contact-message {
        padding-left: 0;
        &:after {
            display: none;
        }
        span {
            width: 40px !important;
        }
    }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    h2 {
        font-size: 20px;
    }
    .hex-container {
        width: 42px;
        height: 44px;
    }
    .headerTop_lang {
        flex-basis: 100%;
        order: -1;
        text-align: left;
    }
    .logo {
        margin: 0 auto;
    }
    .mob-nav-btn {
        top: 10px;
    }
    .mob-locations-list > li {
        flex-basis: 100%;
    }
    .about-text,
    .contacts-links-part {
        padding-left: 14%;
    }
    .product-type-slider-container .lines-decor-cont {
        display: none;
    }
    .product-type-slider-container {
        padding-left: 0;
    }
    .slide-text-container,
    .installation-img-text,
    .production-img-text {
        padding-left: 1%;
    }
    
}
@media only screen and (max-width : 375px) {
    .footer-pages-links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        li {
            flex-basis: 100%;
            margin-bottom: 20px;
            padding: 0 10px !important;
        }
    }
    .footer-contacts_links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
        li {
            flex-basis: 100%;
            width: 100%;
            margin-bottom: 20px;
            &:before {
                display: none !important;
            }
        }
    }
    .footer-bottom {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .copy,
    .creator {
        flex-basis: 100%;
        min-width: 100%;
        margin-bottom: 25px;
        padding-left: 8px;
    }
}
/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
    h2 {
        font-size: 16px;
    }
}
/*==========  Mobile First Method  ==========*/
/* Large Devices, Wide Screens */
@media only screen and (min-width : 1200px) {
    .decor-horizontal:after {
      animation: horizontalSlide 5s linear infinite;
    }
    .hex-1 {
      animation: blink1 5s 1s infinite ;
    }
    .hex-2 {
      animation: blink1 5s 1.5s infinite ;
    }
    .hex-3 {
      animation: blink1 5s 2s infinite ;
    }
    .mapPin {
      animation: brightness 3s infinite;
      &:nth-child(2n) {
        animation-delay: 1s;
      }
      &:nth-child(3n) {
        animation-delay: 2s;
      }
    }
    .pin-img-container:after {
        animation: rotation 5s linear infinite;
        transform-origin: 50% 50%;
    }
    .advantages-list li {
        animation: blink1 10s infinite;
    }
    .narrow-decor-cont {
      &:after {
       animation: lines2 20s .3s infinite;
      }
    }
    .wide-decor-cont {
      &:after {
        animation: lines2 20s infinite;
      }
    }
    .contacts-form-part input[type="submit"] {
        animation: blink1 5s infinite;
    }
    .form-decor {
        animation: blink1 5s infinite;
    }
    .single-hex-container {
        animation: blink1 3s infinite;
    }
    img.slick-slide {
      transition: all 6s;
    }
    img.slick-active  {
      transform: scale(1.1);
    }
}

@media only screen and (min-width : 1920px) {
    .headerNav_list a {
        padding: 18px 20px
    }
    .aboutSlider-section {
        margin-bottom: -50px;
        z-index: 5;
    }
    .about-text-section {
        padding-top: 11rem;
    }
}